@keyframes flicker {
  0% {
    color: #fff;
    text-shadow:
      0px 0px 5px var(--color),
      0px 0px 10px var(--color),
      0px 0px 20px var(--color),
      0px 0px 50px var(--color);
    opacity: 0.5;
    /*初始状态 透明度为0*/
  }

  50% {
    opacity: 1;
    /*初始状态 透明度为0*/
    color: #fff;
    text-shadow:
      0px 0px 5px var(--color),
      0px 0px 10px var(--color),
      0px 0px 20px var(--color),
      0px 0px 50px var(--color);
  }

  100% {
    color: #fff;
    text-shadow:
      0px 0px 5px var(--color),
      0px 0px 10px var(--color),
      0px 0px 20px var(--color),
      0px 0px 50px var(--color);
    opacity: 0.5;
    /*初始状态 透明度为0*/
  }

}

