.bigcontainer {
  padding-top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bigcontainer h1 {
  color: #333;
  margin: 30px;
  font-size: 40px;
  font-weight: 1500;
  font-family: 'font';
}

.container {
  width: 1200px;
  position: relative;
  display: flex;
  justify-content: space-around;
}

.container .card {
  position: relative;


}

.container .card .face {
  width: 350px;
  height: 300px;
  transition: 0.5s;
}

.container .card .face.face1 {
  position: relative;
  background: rgb(247, 247, 247);
  /* display: flex;
  justify-content: center;
  align-items: center; */
  z-index: 1;
  /* overflow-y: hidden; */
  transform: translateY(100px);
}

.container .card .face.face1 .content {
  /* opacity: 0.2; */
  transition: 0.5s;
}

.container .card:hover .face.face1 .content {
  opacity: 1;
}

.container .card .face.face1 .content h3 {
  margin: 10px 0 0;
  padding: 0;

  color: #fff;
  text-align: center;
  font-size: 1.5em;
  font-family: 'name';
}

.container .card:hover .face.face1 {
  position: relative;
  background: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  transform: translateY(0px);
  transition: 0.75s;
}
.container .card:hover .face.face1 .role1 {
  transition: 5.75s;
}

.container .card .face.face2 {
  position: relative;


  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  box-shadow: 0 11px 20px rgba(0, 0, 0, 0.1);
  transform: translateY(-100px);

}

.container .card:hover .face.face2 {
  transform: translateY(0);
}

.container .card:hover .role {
  width: 170px;
  transition: 0.5s;
}

.container .card .role {
  width: 155PX;
}

.container .card:hover .role2 {
  width: 200px;
  transition: 0.5s;
}

.container .card .role2 {
  width: 170PX;
}

.container .card:hover .role1 {
  width: 350px;
  transition: 1.5s;
}

.container .card .role1 {
  width: 350px;
  /* height: 350px; */
}